/*Nick's Stuff*/


.App {
    text-align: center;
}
.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}
.App-header {
    background-color: #001A46;
    height: 150px;
    padding: 20px;
    color: white;
}

.Form{
    margin: 0 auto;
}
.media-object {
    width: 200px;
    height: 200px;
    margin-left: 20px;
}

body{
    font-family: 'Raleway', sans-serif;
}

footer{
    background-color: #001A46;
    padding:20px;
    color: #FFF;
    text-align:center;
    margin:0;
    clear:both;
    bottom:0;
    width:100%;
}

#clients{
    background-color:#EEE;
}

#purchaseArea{
    text-align:center;
}

#tixLogo{
    width: 50px;
    height: 50%;
    border-radius: 5px;
    margin-top: 30px;
    float: left;
}

#siteName{
    margin-top: 30px;
    font-size: 60px;
    font-family: 'Fugaz One', cursive;
    text-align: right; 
}

#mediaContent {
    text-align: left;
    border-left: 20px;
}

#mediaStuff {
    margin-left: 20px;
}

#purchaseNow, #submitPayment {
    margin-bottom: 100px;
    width: 100%;
    height: 50px;
    font-size: 25px; 
}

.selectOption {
    width: 100%;
    margin-top: 5px;
}

#labels, #name, #email, #option {
    margin-left: 5px;
}

#warn {
    display: none;
}


